<template>
  <div class="box">
    <div class="list">
      <div class="listitem" v-for="item in list" :key="item.id">
        <van-checkbox
          v-model="item.ifchecked"
          checked-color="#FD656D"
          @change="setcheck(item)"
          v-show="false"
        ></van-checkbox>
        <img
          class="ifchecked"
          :src="goodsifchecked(item.ifchecked)"
          alt
          @click="item.ifchecked = !item.ifchecked "
        />
        <img :src="item.image" :alt="item.name" />
        <div class="info">
          <div class="line1">
            {{ item.name }}
            <span class="remove" @click="remove()"></span>
            <van-dialog
              v-model="show"
              show-cancel-button
              confirm-button-text="删除"
              confirm-button-color="#ff4a54"
              @confirm="del(item.id)"
              @cancel="cancel"
            >
              <img src="@/assets/all/tips.png" alt />
              <p>确认删除该商品吗？</p>
            </van-dialog>
          </div>
          <div class="line2">
            ￥{{ item.price }}
            <van-stepper input-width="40px" button-size="22px" v-model="item.quantity" />
          </div>
        </div>
      </div>
    </div>

    <div class="submitbar">
      <div class="checkall" @click="setcheckall()">
        <img class="ifchecked" :src="goodsifchecked2(checkall)" alt />
        <span>全选</span>
      </div>
      <div class="datas">
        合计：
        <span>￥{{ totalPrice.toFixed(2) }}</span>
        <div class="btn">去结算({{ checkedlist.length }})</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  data() {
    return {
      show: false,
      list: [
        {
          id: 1,
          name: "俄罗斯美甲",
          image: require("@/assets/homepages/img1.png"),
          price: "99.00",
          quantity: 1,
          ifchecked: false
        },
        {
          id: 2,
          name: "蒙巴萨涂抹式去眼袋（体验价）",
          image: require("@/assets/homepages/img2.png"),
          price: "99.00",
          quantity: 1,
          ifchecked: false
        }
      ],
      checkedlist: [],
      checkall: false
    };
  },
  methods: {
    remove(id) {
      this.show = true;
     
    },
    cancel() {
      this.show = false;

    },
    del(id) {
      this.list = this.list.filter(item => {
        return item.id !== id;
      });
      console.log(this.list)
      this.show = false;
    },
    setcheckall() {
      if (this.checkall) {
        this.list.forEach(item => {
          item.ifchecked = false;
        });
      } else {
        this.list.forEach(item => {
          item.ifchecked = true;
        });
      }
    },
    setcheck(val) {
      if (val.ifchecked) {
        this.checkedlist.push(val);
      } else {
        this.checkedlist.map((item, index) => {
          if (val.id == item.id) {
            this.checkedlist.splice(index, 1);
            return;
          }
        });
      }
    }
  },
  computed: {
    totalPrice() {
      return this.list.reduce((total, item) => {
        return total + (item.ifchecked ? item.price * item.quantity : 0);
      }, 0);
    },
    goodsifchecked: () => data => {
      return data
        ? require("@/assets/all/selected_cart.png")
        : require("@/assets/all/unselected.png");
    },
    goodsifchecked2: () => data => {
      return data
        ? require("@/assets/all/selected_cart.png")
        : require("@/assets/all/unselected.png");
    }
  },
  watch: {
    checkedlist(val) {
      if (this.list.length === val.length) {
        this.checkall = true;
        return;
      } else {
        this.checkall = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  .list {
    background-color: #fff;
    padding: 0 22px;
  }
  .listitem {
    position: relative;
    height: 200px;
    display: flex;
    align-items: center;
    .ifchecked {
      flex: none;
      width: 30px;
      height: 30px;
      margin-right: 22px;
    }
    img {
      border-radius: 10px;
      width: 160px;
      height: 160px;
    }
    .info {
      margin: 35px 0 37px 20px;
      flex: 1;
      box-sizing: border-box;
      .line1 {
        height: 76px;
        font-size: 28px;
        color: #333;
        span {
          position: absolute;
          display: inline-block;
          width: 30px;
          height: 30px;
          background: url("../../../assets/all/del.png") no-repeat;
          background-size: 100% 100%;
          right: 37px;
          top: 37px;
        }
        .van-dialog {
          width: 580px;
          img {
            display: block;
            width: 72px;
            height: 72px;
            margin: 48px auto 20px;
          }
          p {
            text-align: center;
            font-size: 30px;
            line-height: 42px;
            color: #555;
            margin-bottom: 48px;
          }
        }
      }

      .line2 {
        width: 100%;
        display: flex;
        font-size: 30px;
        color: #ff4a54;
        justify-content: space-between;
      }
    }
  }
  .submitbar {
    color: #333;
    font-size: 28px;
    position: fixed;
    bottom: 98px;
    padding: 0 22px;
    box-sizing: border-box;
    background-color: #fff;
    height: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    .checkall {
      img {
        vertical-align: -5px;
        width: 30px;
        height: 30px;
        margin-right: 20px;
      }
    }
    .datas {
      display: flex;
      align-items: center;

      span {
        font-size: 30px;
        color: #fd656d;
      }
      .btn {
        background-color: #fd656d;
        width: 180px;
        line-height: 74px;
        border-radius: 36px;
        color: #fff;
        text-align: center;
        margin-left: 22px;
      }
    }
  }
}
</style>



